.wpbnmposw {

    --bnm-header-font-size: 21px;
    --bnm-header-margin: 0 0 1em;
	--bnm-title-color: #ffffff;
	--bnm-title-hover-color: #dddddd;
	--bnm-title-font-size: 26px;
    --bnm-title-line-height: 1.2;
	--bnm-title-letter-spacing: normal;
    --bnm-title-margin: 0 0 10px 0;
	--bnm-meta-color: #ffffff;
	--bnm-meta-hover-color: #dddddd;
    --bnm-meta-font-size: 14px;
    --bnm-meta-spacing: 15px;
    --bnm-meta-margin: 0 0 15px;
	--bnm-category-color: #ffffff;
	--bnm-category-hover-color: #dddddd;
	--bnm-category-bg-color: #000000;
	--bnm-category-bg-hover-color: #000000;
    --bnm-category-font-size: 13px;
    --bnm-category-bg-hover-color: #eee;
    --bnm-category-margin: 0 10px 8px 0;
	--bnm-category-padding: 2px 8px;

    margin: 0 0 50px 0;

    .bnm-block-title-wrap {
        position: relative;
        margin: var(--bnm-header-margin);
        padding: var(--bnm-header-padding);
    }

    .article-section-title {
        color: var(--bnm-header-color);
		font-size: var(--bnm-header-font-size);
        line-height: var(--bnm-header-line-height);
        letter-spacing: var(--bnm-header-letter-spacing);
        border-bottom: 2px solid #000000;
        margin: 0;
        padding: 0;
	}

    article {
        .entry-title {
            font-size: var(--bnm-title-font-size);
            letter-spacing: var(--bnm-title-letter-spacing);
            line-height: var(--bnm-title-line-height);
            margin: var(--bnm-title-margin);
            padding: var(--bnm-title-padding);
            
            a {
                color: var(--bnm-title-color);
                position: relative;
                text-decoration: none;
                z-index: 2;
    
                &:hover {
                    color: var(--bnm-title-hover-color);
                }
            }
        }

        .entry-meta {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            color: var(--bnm-meta-color);
            font-size: var(--bnm-meta-font-size);
            padding: var(--bnm-meta-padding);
            margin: var(--bnm-meta-margin);
            letter-spacing: var(--bnm-meta-letter-spacing);
            line-height: var(--bnm-meta-line-height);
            text-decoration: inherit;

            a {
                color: var(--bnm-meta-color);
                text-decoration: inherit;
                font-size: var(--bnm-meta-font-size);
                line-height: var(--bnm-meta-line-height);
                letter-spacing: var(--bnm-meta-letter-spacing);
                position: relative;
                z-index: 2;

                &:visited {
                    color: var(--bnm-meta-color);
                }
    
                &:hover {
                    color: var(--bnm-meta-hover-color);
                }
            }
        }

        .bnm-category-list {

            font-size: var(--bnm-category-font-size);

            a {
                color: var(--bnm-category-color);
                margin: var(--bnm-category-margin);
                text-decoration: none;
    
                &:hover {
                    color: var(--bnm-category-hover-color);
                }
            }
        }
    }

    .byline,
    .posted-on,
    .bnm-comment-count {
        
        margin-right: var(--bnm-meta-spacing);

        a {
            text-decoration: none;
            position: relative;
			z-index: 2;
            color: var(--bnm-meta-color);

            &:hover {
                color: var(--bnm-meta-hover-color);
            }
        }
    }

    .bnm-category-list {
        display: flex;
		flex-wrap: wrap;

		a {
			display: inline-block;
			position: relative;
			z-index: 2;
            font-weight: bold;
		}
	}

	&.bnm-box-cat {
		article .bnm-category-list {
			a {
				background-color: var(--bnm-category-bg-color);
				padding: var(--bnm-category-padding);

				&:hover {
					background-color: var(--bnm-category-bg-hover-color);
				}
			}
		}
	}

    .post-thumbnail {
		margin: 0;
		height: 100%;
		width: 100%;
		padding: 0;
		position: relative;
		a,
		img {
			display: block;
			height: 100%;
			object-fit: cover;
			width: 100%;

			&.image-fit-contain {
				object-fit: contain;
			}
		}
	}

    .bnm-slider-content {
		bottom: 0;
		background: linear-gradient(0deg,rgba(0,0,0,0.7),rgba(0,0,0,0) 100%,rgba(0,0,0,0));
		color: var(--bnm-meta-color);
		left: 0;
		padding: 1.5em;
		position: absolute;
		right: 0;
	}

	&.hide-pagination {
		.bnm-swiper-pagination {
			display: none;
		}
	}

	&.hide-next-prev-btns {
		.bnm-swiper-btn-prev,
		.bnm-swiper-btn-next {
			display: none;
		}
	}

	.bnm-img-placeholder {
		height: 420px;
		background: #f8f8f8;
	}

    .swiper-slide {
		height: auto;
		max-height: 100vh;
	}

	.swiper-button-next, 
	.swiper-button-prev {
		color: #ffffff !important;
		width: 45px;
        height: 45px;
		background-color: rgba(0, 0, 0, 0.5);
        border-radius: 5px;

		&:after {
			font-size: 20px !important;
		}
	}

    .swiper-button-next, .swiper-rtl .swiper-button-prev {
        right: 27px;
        left: auto;
    }

    .swiper-button-prev, .swiper-rtl .swiper-button-next {
        left: 27px;
        right: auto;
    }

    .thbnm-swiper {
        .swiper-pagination-bullet {
            background-color: #cccccc;
        }
    
        .swiper-pagination-bullet-active {
            background-color: #ffffff;
        }
    }

}

.updated:not(.published) {
	display: none;
}

.wpbnmposw:hover .bnm-slider-content {
	background: linear-gradient(0deg,rgba(0,0,0,0.8),rgba(0,0,0,0) 100%,rgba(0,0,0,0));
}

.bnmslovrlay.inside-gut-editor {
	cursor: pointer
}